.partner {
  background-image: url("../images/hubspot-development.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  background-attachment: scroll;
  width: 100%;
  padding: 140px 0px;
}
.partner__wraper {
  display: flex;
  justify-content: flex-end;
}
.partner__text {
  max-width: 429px;
  width: 100%;
  padding: 0 20px;
}
.partner__texts-slogan {
  color: #fff;
  font-family: Inter Tight, sans-serif;
  font-size: 18px;
}
.partner__texts-title {
  color: #fff;
  font-family: Raleway, sans-serif;
  font-size: 38px;
  line-height: 48px;
  margin-top: 15px;
}
.partner__text-subtitle {
  color: #fff;
  margin-bottom: 30px;
  font-family: "Plus Jakarta Sans", sans-serif;
  font-size: 18px;
}
.partner__text-button {
  color: #fff;
  width: 260px;
  text-align: center;
  background-color: #d25658;
  border: 0 solid #d25658;
  border-radius: 10px;
  padding: 20px 28px;
  font-family: Plus Jakarta Sans, sans-serif;
  font-size: 18px;
  transition-duration: 0.3s;
  &:hover {
    transform: scale(1.1);
    background-color: #e7090c;
  }
}
@media screen and (max-width: 767px) {
  .partner {
    background-image: none;
    background-color: #56206e;
    padding: 80px 0px;
  }
  .partner__text {
    max-width: 100%;
  }
  .partner__texts-slogan {
    font-size: 16px;
  }
  .partner__texts-title {
    font-size: 30px;
    padding: 0;
    margin: 0;
    margin-bottom: 16px;
  }
  .partner__text-subtitle {
    font-size: 16px;
    line-height: 27px;
  }
  .partner__text-button {
    width: 210px;
    font-size: 15px;
  }
}
